      <span v-if="todoCount" slot="center"
        >çœ‹ç€é‚£ä¹ˆå¤šçš„å¾…åŠæ²¡åšï¼Œéš¾é“ä½ ğŸ’•ä¸ä¼šç—›å—</span
      >
      <span v-else slot="center">åŠ æ²¹ï¼Œä»Šæ—¥äº‹ä»Šæ—¥æ¯•â™¥</span>
    </nav-bar>
    <task-bar>
      <span slot="tagging" v-if="todoCount"
        >ç®—ä¸‹æ¥ï¼Œä½ å·²ç»æœ‰<span style="color: #ff0000">{{ todoCount }}</span
        >ä»¶äº‹æ‹–ç€æ²¡å¹²å•¦</span
      >
      <span slot="tagging" v-else
        >å¾ˆæ£’ï¼Œå‰å‡ å¤©æ²¡æœ‰ç•™ä¸‹æœªå®Œæˆå¾…åŠï¼Œä»Šå¤©ç»§ç»­åŠ æ²¹</span
      >
      <transition-group name="ani">
