<script>
import NavBar from "components/common/navbar/NavBar.vue";
import TaskBar from "components/content/taskbar/TaskBar.vue";
import ListItem from "components/content/listItem/ListItem.vue";

import {
  getData,
  saveData,
  deleteData,
  getDateStr,
} from "assets/js/todoList.js";

export default {
  name: "Todo",
  components: {
    NavBar,
    TaskBar,
    ListItem,
  },
  data() {
    return {
      myNoTodo: [
        // {title: 'ğŸåƒè‹¹æœ', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸ“åƒè‰è“', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸŒåƒé¦™è•‰', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸˆåƒæ©˜å­', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸ‰åƒè¥¿ç“œ', done: false, createDate: "2021-03-26"},
      ],
      dateNow: "",
    };
  },
  computed: {
    //æœªå®Œæˆæ•°é‡
    todoCount() {
      return this.myNoTodo.filter((item) => {
        return !item.done;
      }).length;
    },
  },
  // é¡µé¢åŠ è½½å³æ¸²æŸ“æ‰€æœ‰æ•°æ®å¹¶ä¸”å¾—åˆ°å½“å‰æ—¥æœŸ
  created() {
    let allList = getData("myNoTodo");
    this.myNoTodo = allList;
    this.dateNow = getDateStr();
  },
  watch: {
    myNoTodo: {
      handler() {
        saveData(this.myNoTodo, "myNoTodo");
      },
      deep: true, //æ·±ç›‘å¬
    },
  },
  methods: {
    deleteItem(index) {
      this.myNoTodo.splice(index, 1);
    },
    toDo(index) {
      let myDay = getData("myday");

      this.myNoTodo[index].createDate = this.dateNow;
      myDay.push(this.myNoTodo[index]);
      saveData(myDay, "myday");
      this.myNoTodo.splice(index, 1);
    },
  },
};
</script>

